import util from "../../utils/util.js";
import api from "../../utils/api.js";
export default {
	data() {
		return {
			isCheck: false,
			isFamily: false,
			nodes: '',
			goBack: 0
		};
	},
	onLoad(options) {
		if (options.familyStatus == 1) {
			this.$data.isFamily = true;
			this.$data.isCheck  = true;
			if (options.goBack == 1) {
				this.$data.goBack = 1
			}
		}
		console.log(this.$data.isFamily);
		
		
	},
	onShow() {
		this.getInviteCode();
	},
	methods: {
		/**
		 * 是否同意
		 */
		doCheck() {
			this.$data.isCheck = !this.$data.isCheck
		},
		/**
		 * 注册或分享
		 */
		register() {
			// console.log('注册');
			if (this.$data.isFamily) {
				if (this.$data.goBack == 1) {
					uni.navigateBack({
						delta: 1
					})
				} else {
					uni.navigateTo({
						url: '../familyCenter/familyCenter',
					})
				}

			} else {
				if (this.$data.isCheck) {
					uni.navigateTo({
						url: '../family/family',
					})
				} else {
					util.successTips('请阅读同意说明');
				}
			}

		},
		/**
		 * 获取用户小路人家信息
		 */
		getInviteCode() {
			api.getInviteCode({}).then((res) => {
				if (res.code == 200 || res.code == 0) {
					this.$data.nodes = res.data.text
				}
			})
		},
	}
}
